<template>
    <div class="container">
        <Title greeting-message="相關連結"></Title>
        
        <b-tabs content-class="mt-3" class="my-4">
            <b-tab title="相關連結" active>
                <div v-for="(item, index) in link.relevance" :key="index">
                    <a :href="item.url">{{ item.name }}</a>
                </div>
            </b-tab>
            <b-tab title="學生系會">
                <div v-for="(item, index) in link.student_council" :key="index">
                    <a :href="item.url">{{ item.name }}</a>
                </div>
            </b-tab>
            <b-tab title="全國科技大學相關學院">
                <fieldset>
                    <legend>北部地區</legend>
                    <div v-for="(item, index) in link.university.associate_tech_university_north" :key="index">
                        <a :href="item.url">{{ item.name }}</a>
                    </div>
                </fieldset>
                <fieldset>
                    <legend>中部地區</legend>
                    <div v-for="(item, index) in link.university.associate_tech_university_central" :key="index">
                        <a :href="item.url">{{ item.name }}</a>
                    </div>
                </fieldset>
                <fieldset>
                    <legend>南部地區</legend>
                    <div v-for="(item, index) in link.university.associate_tech_university_south" :key="index">
                        <a :href="item.url">{{ item.name }}</a>
                    </div>
                </fieldset>
                <fieldset>
                    <legend>離島地區</legend>
                    <div v-for="(item, index) in link.university.associate_tech_university_land" :key="index">
                        <a :href="item.url">{{ item.name }}</a>
                    </div>
                </fieldset>
                
                
                
            </b-tab>
            <b-tab title="本校資訊">
                <div v-for="(item, index) in link.information" :key="index">
                    <a :href="item.url">{{ item.name }}</a>
                </div>
            </b-tab>
        </b-tabs>
        <div class="text-center">
            <button type="button" class="btn btn-secondary my-3" @click="historyPrev">回上頁</button>
        </div>
    </div>
</template>

<script>
import Title from '../components/Title.vue';
import { LinkServer } from "../../providers/http-server.js";

export default {
    name: 'App',
    data(){
        return {
            link: {
                relevance: [],
                student_council: [],
                university: {
                    associate_tech_university_north: [],
                    associate_tech_university_central: [],
                    associate_tech_university_south: [],
                    associate_tech_university_land: []
                },
                information: []
            }
        }
    },
    components: {
        Title
    },
    created(){
        LinkServer.read({group: true}).then(
            (response) => {
                this.link.relevance = response.data.relevance;
                this.link.student_council = response.data.student_council;
                this.link.university.associate_tech_university_north = response.data.associate_tech_university_north;
                this.link.university.associate_tech_university_central = response.data.associate_tech_university_central;
                this.link.university.associate_tech_university_south = response.data.associate_tech_university_south;
                this.link.university.associate_tech_university_land = response.data.associate_tech_university_land;
                this.link.information = response.data.information;
            }
        )
    },
    methods: {
        historyPrev() {
            this.$router.go(-1);
        }
    }
}
</script>

<style scoped></style>